.App {
  min-height: 100vh;
}
.splitScreen {
  display: flex;
  flex-direction: column;
}
.leftPane {
  height: 100%;
  position: fixed;
  z-index: 1;
  top: 0;
  overflow-x: hidden;
  padding-top: 20px;
  left: 0;
  background-color: red;
  width: 10%;
}
.rightPane {
  height: 100%;
  position: fixed;
  z-index: 1;
  top: 0;
  overflow-x: hidden;
  padding-top: 20px;
  right: 0;
  width: "70%";
}
